.container {
  width: calc(100% - 20px);
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
}

.list {
  width: 100%;
  padding: 0;
  margin-top: 20px;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    gap: 4px;
  }
  @media screen and (min-width: 1200px) {
    margin-top: 50px;
    gap: 18px;
    justify-content: center;
  }
}

.item {
  display: block;
  min-height: 115px;
  height: min-content;
  background-color: azure;
  background-color: rgba(254, 205, 165, 0.2);
  background-color: #363636c5;
  list-style-type: none;
  margin-top: 15px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 15px;
  border-radius: 10px;
  border: 4px double #dcdcdc;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3), 0px 4px 4px rgba(0, 0, 0, 0.4),
    1px 4px 6px rgba(0, 0, 0, 0.4);
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 768px) {
    width: calc(100% / 2 - 30px);
    min-height: 125px;
  }
  @media screen and (min-width: 1200px) {
    width: calc(100% / 3 - 40px);
    margin-top: 0;
  }
}

.item:hover {
  background-color: #484747d0;
  border: 4px double #f7ba06;
}

.item:hover .text {
  color: #f7ba06;
}

.item:hover .icon__quill {
  fill: #f7ba06;
  border: 2px solid #f7ba06;
}

.icon__quill {
  display: block;
  width: 22px;
  height: 22px;
  fill: #665d52;
  fill: #dcdcdc;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  border: 2px solid #665d52;
  border: 2px solid #dcdcdc;
  border-radius: 50%;
  padding: 2px;
}

.text {
  font-family: 'Playfair Display', serif;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  color: #665d52;
  color: #dcdcdc;
  text-decoration: none;
}
