.container {
  width: calc(100% - 20px);
  margin-left: auto;
  margin-right: auto;
  padding-top: 20px;
  max-width: 1400px;
}

.text {
  font-family: 'Playfair Display', serif;
  font-size: 24px;
  font-weight: 700;
  text-align: center;
  color: #dcdcdc;
  color: #e3a404;
}

.img {
  width: 100%;
  border-radius: 8px;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3), 0px 4px 4px rgba(0, 0, 0, 0.4),
    1px 4px 6px rgba(0, 0, 0, 0.4);
}
